import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { Play, Clock, Users, Trophy, Zap, Filter } from 'lucide-react';

const QuickJoin = ({ availableGames = [], onJoinGame, userProfile }) => {
  const [filteredGames, setFilteredGames] = useState([]);
  const [filters, setFilters] = useState({
    difficulty: 'all',
    entryFee: 'all',
    maxPlayers: 'all'
  });
  const [sortBy, setSortBy] = useState('startTime');

  useEffect(() => {
    let games = [...availableGames];
    
    // Apply filters
    if (filters.difficulty !== 'all') {
      games = games.filter(game => game.difficulty === filters.difficulty);
    }
    if (filters.entryFee !== 'all') {
      games = games.filter(game => game.entryFee <= parseInt(filters.entryFee));
    }
    if (filters.maxPlayers !== 'all') {
      games = games.filter(game => game.maxPlayers <= parseInt(filters.maxPlayers));
    }
    
    // Apply sorting
    games.sort((a, b) => {
      switch (sortBy) {
        case 'startTime':
          return a.startTime - b.startTime;
        case 'entryFee':
          return a.entryFee - b.entryFee;
        case 'currentPlayers':
          return b.currentPlayers - a.currentPlayers;
        case 'difficulty':
          const difficultyOrder = { easy: 1, medium: 2, hard: 3 };
          return difficultyOrder[a.difficulty] - difficultyOrder[b.difficulty];
        default:
          return 0;
      }
    });
    
    setFilteredGames(games);
  }, [availableGames, filters, sortBy]);

  const formatTimeUntilStart = (startTime) => {
    const now = Date.now();
    const timeLeft = startTime - now;
    
    if (timeLeft <= 0) return 'Starting now...';
    
    const minutes = Math.floor(timeLeft / 60000);
    const seconds = Math.floor((timeLeft % 60000) / 1000);
    
    if (minutes > 0) {
      return `${minutes}m ${seconds}s`;
    }
    return `${seconds}s`;
  };

  const getDifficultyIcon = (difficulty) => {
    switch (difficulty) {
      case 'easy': return '😊';
      case 'medium': return '😐';
      case 'hard': return '😤';
      default: return '❓';
    }
  };

  const getDifficultyColor = (difficulty) => {
    switch (difficulty) {
      case 'easy': return 'text-green-400';
      case 'medium': return 'text-yellow-400';
      case 'hard': return 'text-red-400';
      default: return 'text-gray-400';
    }
  };

  const getStatusColor = (status) => {
    switch (status) {
      case 'waiting': return 'text-green-400';
      case 'starting': return 'text-yellow-400';
      case 'playing': return 'text-blue-400';
      case 'finished': return 'text-gray-400';
      default: return 'text-gray-400';
    }
  };

  const canJoinGame = (game) => {
    return userProfile.coins >= game.entryFee && 
           game.currentPlayers < game.maxPlayers && 
           game.status === 'waiting';
  };

  return (
    <div className="quick-join-container">
      <motion.div
        className="text-center mb-8"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6 }}
      >
        <h2 className="text-3xl font-bold gradient-text mb-4">Quick Join Matches</h2>
        <p className="text-gray-300 text-lg">Join tournaments that are starting soon!</p>
      </motion.div>

      {/* Filters and Sorting */}
      <div className="card mb-8">
        <div className="flex flex-wrap gap-4 items-center justify-between">
          <div className="flex gap-4">
            {/* Difficulty Filter */}
            <select
              value={filters.difficulty}
              onChange={(e) => setFilters(prev => ({ ...prev, difficulty: e.target.value }))}
              className="input-box"
            >
              <option value="all">All Difficulties</option>
              <option value="easy">Easy</option>
              <option value="medium">Medium</option>
              <option value="hard">Hard</option>
            </select>

            {/* Entry Fee Filter */}
            <select
              value={filters.entryFee}
              onChange={(e) => setFilters(prev => ({ ...prev, entryFee: e.target.value }))}
              className="input-box"
            >
              <option value="all">Any Entry Fee</option>
              <option value="1">$1</option>
              <option value="5">$5</option>
              <option value="10">$10</option>
              <option value="25">$25</option>
              <option value="50">$50</option>
            </select>

            {/* Max Players Filter */}
            <select
              value={filters.maxPlayers}
              onChange={(e) => setFilters(prev => ({ ...prev, maxPlayers: e.target.value }))}
              className="input-box"
            >
              <option value="all">Any Size</option>
              <option value="8">8 Players</option>
              <option value="16">16 Players</option>
              <option value="24">24 Players</option>
              <option value="32">32 Players</option>
              <option value="50">50 Players</option>
            </select>
          </div>

          {/* Sort By */}
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="input-box"
          >
            <option value="startTime">Sort by Start Time</option>
            <option value="entryFee">Sort by Entry Fee</option>
            <option value="currentPlayers">Sort by Players</option>
            <option value="difficulty">Sort by Difficulty</option>
          </select>
        </div>
      </div>

      {/* Available Games */}
      {filteredGames.length === 0 ? (
        <div className="text-center py-16">
          <div className="text-6xl mb-4">🎯</div>
          <h3 className="text-xl font-bold mb-2">No matches available</h3>
          <p className="text-gray-400 mb-6">
            {availableGames.length === 0 
              ? "No tournaments have been created yet." 
              : "No matches match your current filters."}
          </p>
          {availableGames.length === 0 && (
            <motion.button
              onClick={() => window.location.reload()}
              className="btn btn-primary flex items-center gap-2 mx-auto"
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
            >
              <Zap className="w-4 h-4" />
              Refresh
            </motion.button>
          )}
        </div>
      ) : (
        <div className="grid grid-cols-1 md-grid-cols-2 lg-grid-cols-3 gap-6">
          {filteredGames.map((game) => (
            <motion.div
              key={game.id}
              className={`card transition-all ${
                canJoinGame(game) 
                  ? 'hover:scale-105 cursor-pointer border-primary' 
                  : 'opacity-75 border-gray-600'
              }`}
              whileHover={canJoinGame(game) ? { scale: 1.02 } : {}}
              whileTap={canJoinGame(game) ? { scale: 0.98 } : {}}
              onClick={canJoinGame(game) ? () => onJoinGame(game.id) : undefined}
            >
              <div className="flex justify-between items-start mb-4">
                <div className="flex items-center gap-2">
                  <span className="text-2xl">{getDifficultyIcon(game.difficulty)}</span>
                  <span className={`font-semibold ${getDifficultyColor(game.difficulty)}`}>
                    {game.difficulty.charAt(0).toUpperCase() + game.difficulty.slice(1)}
                  </span>
                </div>
                <span className={`text-sm font-medium ${getStatusColor(game.status)}`}>
                  {game.status.charAt(0).toUpperCase() + game.status.slice(1)}
                </span>
              </div>

              <h3 className="text-lg font-bold mb-2">{game.name}</h3>
              
              <div className="space-y-2 mb-4">
                <div className="flex justify-between text-sm">
                  <span className="text-gray-400">Rounds:</span>
                  <span className="font-medium">{game.rounds}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-400">Entry Fee:</span>
                  <span className="font-medium text-primary">${game.entryFee}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-400">Players:</span>
                  <span className="font-medium">{game.currentPlayers}/{game.maxPlayers}</span>
                </div>
                <div className="flex justify-between text-sm">
                  <span className="text-gray-400">Starts in:</span>
                  <span className="font-medium text-warning">
                    {formatTimeUntilStart(game.startTime)}
                  </span>
                </div>
              </div>

              <div className="flex justify-between items-center">
                <span className="text-xs text-gray-400">by {game.createdBy}</span>
                {canJoinGame(game) ? (
                  <motion.button
                    className="btn btn-primary text-sm px-4 py-2"
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                  >
                    Join
                  </motion.button>
                ) : (
                  <div className="text-sm text-gray-500">
                    {userProfile.coins < game.entryFee ? 'Not enough coins' : 
                     game.currentPlayers >= game.maxPlayers ? 'Full' : 
                     game.status !== 'waiting' ? 'Not accepting' : 'Cannot join'}
                  </div>
                )}
              </div>
            </motion.div>
          ))}
        </div>
      )}

      {/* Game Stats */}
      {filteredGames.length > 0 && (
        <motion.div
          className="mt-8 text-center"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6, delay: 0.4 }}
        >
          <div className="inline-flex items-center gap-8 text-sm text-gray-400">
            <div className="flex items-center gap-2">
              <Clock className="w-4 h-4" />
              {filteredGames.filter(g => g.status === 'waiting').length} waiting to start
            </div>
            <div className="flex items-center gap-2">
              <Users className="w-4 h-4" />
              {filteredGames.reduce((sum, g) => sum + g.currentPlayers, 0)} total players
            </div>
            <div className="flex items-center gap-2">
              <Trophy className="w-4 h-4" />
              ${filteredGames.reduce((sum, g) => sum + (g.entryFee * g.maxPlayers), 0)} total prize pools
            </div>
          </div>
        </motion.div>
      )}
    </div>
  );
};

export default QuickJoin;
