import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { motion, AnimatePresence } from 'framer-motion';
import Home from './pages/Home';
import Play from './pages/Play';
import Leaderboard from './pages/Leaderboard';
import Packs from './pages/Packs';
import QuickJoin from './components/QuickJoin';
import TournamentMatch from './components/TournamentMatch';
import CreateGame from './components/CreateGame';
import './styles/theme.css';

function App() {
  const [currentView, setCurrentView] = useState('home');
  const [activeMatch, setActiveMatch] = useState(null);
  const [availableGames, setAvailableGames] = useState([]);
  const [userProfile, setUserProfile] = useState({
    id: 'user_' + Date.now(),
    username: 'Player' + Math.floor(Math.random() * 1000),
    coins: 1000,
    totalWinnings: 0,
    matchesPlayed: 0,
    bestScore: 0
  });

  // Generate some sample games for testing
  useEffect(() => {
    const sampleGames = [
      {
        id: 'game_1',
        name: 'Quick Match - Easy',
        difficulty: 'easy',
        rounds: 5,
        entryFee: 1,
        maxPlayers: 8,
        currentPlayers: 3,
        status: 'waiting',
        startTime: Date.now() + 30000, // 30 seconds from now
        createdBy: 'Player123'
      },
      {
        id: 'game_2',
        name: 'Tournament - Medium',
        difficulty: 'medium',
        rounds: 8,
        entryFee: 5,
        maxPlayers: 16,
        currentPlayers: 7,
        status: 'waiting',
        startTime: Date.now() + 60000, // 1 minute from now
        createdBy: 'Player456'
      },
      {
        id: 'game_3',
        name: 'Championship - Hard',
        difficulty: 'hard',
        rounds: 10,
        entryFee: 25,
        maxPlayers: 32,
        currentPlayers: 12,
        status: 'waiting',
        startTime: Date.now() + 120000, // 2 minutes from now
        createdBy: 'Player789'
      }
    ];
    setAvailableGames(sampleGames);
  }, []);

  // Create a new game
  const handleCreateGame = async (gameConfig) => {
    try {
      if (userProfile.coins < gameConfig.entryFee) {
        alert('Not enough coins to create this game!');
        return;
      }

      const newGame = {
        id: 'game_' + Date.now(),
        name: gameConfig.name,
        difficulty: gameConfig.difficulty,
        rounds: gameConfig.rounds,
        entryFee: gameConfig.entryFee,
        maxPlayers: gameConfig.maxPlayers,
        currentPlayers: 1, // Creator joins automatically
        status: 'waiting',
        startTime: Date.now() + (gameConfig.startDelay * 1000),
        createdBy: userProfile.username,
        players: [userProfile.id]
      };

      setAvailableGames(prev => [newGame, ...prev]);
      setCurrentView('home');
      alert('Game created successfully!');
    } catch (error) {
      console.error('Failed to create game:', error);
      alert('Failed to create game. Please try again.');
    }
  };

  // Join an existing game
  const handleJoinGame = async (gameId) => {
    try {
      const game = availableGames.find(g => g.id === gameId);
      if (!game) {
        alert('Game not found!');
        return;
      }

      if (userProfile.coins < game.entryFee) {
        alert('Not enough coins to join this game!');
        return;
      }

      if (game.currentPlayers >= game.maxPlayers) {
        alert('Game is full!');
        return;
      }

      // Deduct entry fee
      setUserProfile(prev => ({
        ...prev,
        coins: prev.coins - game.entryFee
      }));

      // Update game
      setAvailableGames(prev => prev.map(g => 
        g.id === gameId 
          ? { ...g, currentPlayers: g.currentPlayers + 1, players: [...(g.players || []), userProfile.id] }
          : g
      ));

      // Create match for the joined game
      const match = {
        id: 'match_' + Date.now(),
        gameId: gameId,
        difficulty: game.difficulty,
        rounds: game.rounds,
        entryFee: game.entryFee,
        maxPlayers: game.maxPlayers,
        currentPlayers: game.currentPlayers + 1,
        status: 'waiting',
        puzzles: generatePuzzles(game.difficulty, game.rounds),
        players: [...(game.players || []), userProfile.id]
      };

      setActiveMatch(match);
      setCurrentView('tournament');
    } catch (error) {
      console.error('Failed to join game:', error);
      alert('Failed to join game. Please try again.');
    }
  };

  // Generate puzzles based on difficulty
  const generatePuzzles = (difficulty, rounds) => {
    const puzzleSets = {
      easy: [
        { symbols: '🌊🔥🌪️🌍', answer: 'four elements' },
        { symbols: '🌸🌺🌻🌹', answer: 'beautiful flowers' },
        { symbols: '🐺🦁🐯🐻', answer: 'wild predators' },
        { symbols: '🚗✈️🚢🚁', answer: 'transportation' },
        { symbols: '💻📱⌚📺', answer: 'digital devices' },
        { symbols: '🍕🍔🌮🍜', answer: 'fast food' },
        { symbols: '⚽🏀🏈⚾', answer: 'sports balls' },
        { symbols: '🎨🎭🎪🎬', answer: 'entertainment' }
      ],
      medium: [
        { symbols: '🌍🌙⭐☀️', answer: 'celestial bodies' },
        { symbols: '🎭🎪🎨🎬', answer: 'performing arts' },
        { symbols: '🏛️🗽🗼🏰', answer: 'famous monuments' },
        { symbols: '🎸🎹🥁🎺', answer: 'musical instruments' },
        { symbols: '🌋🏔️🏝️🏜️', answer: 'natural landscapes' },
        { symbols: '🦅🦉🦆🦇', answer: 'flying creatures' },
        { symbols: '⚡💧🔥💨', answer: 'natural forces' },
        { symbols: '🎯🎲🎮🎪', answer: 'games and fun' }
      ],
      hard: [
        { symbols: '🧬🔬⚗️🧪', answer: 'scientific research' },
        { symbols: '🌌🪐🛸👽', answer: 'space exploration' },
        { symbols: '🏛️📚🎓⚖️', answer: 'academic institutions' },
        { symbols: '🎭🎪🎨🎬', answer: 'cultural arts' },
        { symbols: '🌋🏔️🏝️🏜️', answer: 'geological formations' },
        { symbols: '🦅🦉🦆🦇', answer: 'nocturnal birds' },
        { symbols: '⚡💧🔥💨', answer: 'elemental forces' },
        { symbols: '🎯🎲🎮🎪', answer: 'recreational activities' }
      ]
    };

    const puzzles = puzzleSets[difficulty] || puzzleSets.medium;
    return puzzles.slice(0, rounds);
  };

  // Handle match completion
  const handleMatchComplete = (matchResults) => {
    const { totalScore, rounds } = matchResults;
    
    // Calculate winnings (mock - in production, integrate with payment system)
    const correctRounds = rounds.filter(r => r.status === 'correct').length;
    const winnings = Math.floor(correctRounds * 50); // $50 per correct round
    
    setUserProfile(prev => ({
      ...prev,
      coins: prev.coins + winnings,
      totalWinnings: prev.totalWinnings + winnings,
      matchesPlayed: prev.matchesPlayed + 1,
      bestScore: Math.max(prev.bestScore, totalScore)
    }));

    // Show results
    alert(`Match Complete! You earned ${winnings} coins with a score of ${totalScore}!`);
  };

  // Handle exiting match
  const handleExitMatch = () => {
    setActiveMatch(null);
    setCurrentView('home');
  };

  // Render current view
  const renderCurrentView = () => {
    switch (currentView) {
      case 'home':
        return (
          <Home 
            onNavigate={setCurrentView} 
            userProfile={userProfile}
            availableGames={availableGames}
            onJoinGame={handleJoinGame}
            onCreateGame={() => setCurrentView('create')}
          />
        );
      case 'play':
        return <Play onNavigate={setCurrentView} />;
      case 'leaderboard':
        return <Leaderboard onNavigate={setCurrentView} />;
      case 'packs':
        return <Packs onNavigate={setCurrentView} />;
      case 'quickjoin':
        return (
          <div className="min-h-screen py-8 px-4">
            <div className="max-w-6xl mx-auto">
              <motion.button
                onClick={() => setCurrentView('home')}
                className="btn btn-outline mb-6 flex items-center gap-2"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                ← Back to Home
              </motion.button>
              <QuickJoin 
                availableGames={availableGames}
                onJoinGame={handleJoinGame}
                userProfile={userProfile}
              />
            </div>
          </div>
        );
      case 'create':
        return (
          <div className="min-h-screen py-8 px-4">
            <div className="max-w-4xl mx-auto">
              <motion.button
                onClick={() => setCurrentView('home')}
                className="btn btn-outline mb-6 flex items-center gap-2"
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
              >
                ← Back to Home
              </motion.button>
              <CreateGame onCreateGame={handleCreateGame} userProfile={userProfile} />
            </div>
          </div>
        );
      case 'tournament':
        return (
          <TournamentMatch
            match={activeMatch}
            onMatchComplete={handleMatchComplete}
            onExitMatch={handleExitMatch}
          />
        );
      default:
        return <Home onNavigate={setCurrentView} userProfile={userProfile} availableGames={availableGames} onJoinGame={handleJoinGame} onCreateGame={() => setCurrentView('create')} />;
    }
  };

  return (
    <Router>
      <div className="App">
        <AnimatePresence mode="wait">
          <motion.div
            key={currentView}
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -20 }}
            transition={{ duration: 0.3 }}
          >
            {renderCurrentView()}
          </motion.div>
        </AnimatePresence>
      </div>
    </Router>
  );
}

export default App;
